import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';

import NewSongList from './newsonglist/newsonglist';
import RecomendList from './recomendlist/recomendlist';

import './homepage.scss';

class NavBar extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className='homepage-navbar'>
                <NavLink activeClassName="on"  exact to="/"></NavLink>
                <NavLink activeClassName="on" to="/singer/"></NavLink>
                <NavLink activeClassName="on" to="/album/"></NavLink>
                <NavLink  to="/"></NavLink>
                <NavLink  to="/"></NavLink>
                <NavLink  to="/"></NavLink>
                <NavLink  to="/">NV</NavLink>
                <NavLink  to="/"></NavLink>
            </div>
        )
    }
}


class HomePage extends Component {
    constructor(props){
        super(props);
    }

    render() {
        return(
        <div className="home-page">
            <NavBar />
            <RecomendList />
            
            <NewSongList />
            <NewSongList />
        </div>
        )
        
    }
}

export default HomePage;